import Cookie from 'js-cookie';

const TOKEN_KEY = 'token';

class Auth {
    constructor() {
        
    }

    static getToken () {
        return Cookie.get(TOKEN_KEY);
    }

    static setToken (v) {
        var exp = new Date();
        //cookie保存7天
        exp.setTime(exp.getTime() + 7 * 24 * 60 * 60 * 1000);
        document.cookie = TOKEN_KEY + "=" + escape(v) + ";expires=" + exp.toGMTString() + "; path=/;"  ;
       // return Cookie.set(TOKEN_KEY, v,exp)
    }
    static delToken (){
        document.cookie = TOKEN_KEY +"=''";
    }
}

export default Auth;



