<template>
    <div class="network-article">
        <div class="tile is-ancestor">
            <div class="tile is-parent">
                <article class="tile is-child box">
                    <h4 class="title is-4">稿件审核概况</h4>
                    <div class="view-rest">
                        <p><span>当前剩余未审核稿件: </span> <span v-text="stat.total_num"></span></p>
                        <p><span>今日新增待审核稿件: </span> <span v-text="stat.today_num"></span></p>
                        <p><span>今日完成稿件: </span> <span v-text="stat.today_audit"></span></p>
                    </div>
                    <div class="mb">
                        <div class="filter-item">
                            分类:
                            <el-select
                                    v-model="classifySelect"
                                    filterable
                                    clearable
                                    placeholder="不选择">
                                <el-option
                                        v-for="(item, index) in classifyList"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                        </div>
                        <div class="filter-item">
                            来源:
                            <el-select v-model="sourceSelect"
                                       filterable
                                       clearable
                                       placeholder="不选择">
                                <el-option
                                        v-for="(item,index) in sourceList"
                                        :key="item.id"
                                        :label="item.name"
                                        :value="item.id">
                                </el-option>
                            </el-select>
                        </div>
                        <div class="filter-item">
                            <el-button type="primary"
                                       :disabled="disabled"
                                       @click="entryAudit">进入审核
                            </el-button>
                        </div>
                    </div>
                </article>
            </div>
        </div>
        <div class="tile is-ancestor">
            <div class="tile is-parent">
                <article class="tile is-child box">
                    <h4 class="title is-4">列表</h4>

                    <div class=" control date-range">
                        <div class="filter-item">
                            <el-date-picker
                                    v-model="dateRange"
                                    @change="search"
                                    type="daterange"
                                    placeholder="选择日期范围">
                            </el-date-picker>
                        </div>
                        <div class="filter-item">
                            <el-input
                                    placeholder="标题关键字"
                                    @keyup.enter.native="search"
                                    v-model="filter.title">
                            </el-input>
                        </div>
                        <div class="filter-item">
                            <el-button type="primary"
                                       @click="search">搜索
                            </el-button>
                        </div>
                    </div>

                    <div class="control table-content">
                        <el-table v-loading="loading"
                                  element-loading-text="拼命加载中"
                                  :data="list"
                                  style="width: 100%">
                            <el-table-column
                                    prop="id"
                                    width="100"
                                    label="ID">
                            </el-table-column>
                            <el-table-column
                                    prop="title"
                                    min-width="180"
                                    label="标题">
                            </el-table-column>
                            <el-table-column
                                    prop="ch_name"
                                    width="80"
                                    label="分类">
                            </el-table-column>
                            <el-table-column
                                    width="100"
                                    label="来源">
                                <template scope="scope">{{ sourceType[scope.row.source_type] }}</template>
                            </el-table-column>
                            <el-table-column
                                    prop="create_time"
                                    label="提交时间">
                            </el-table-column>
                            <el-table-column
                                    label="操作"
                                    width="100">
                                <template scope="scope">
                                    <el-button
                                            @click.native.prevent="toView(scope.row.content_id)"
                                            type="primary"
                                            size="small">
                                        进入审核
                                    </el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>

                    <div class="control">
                        <el-pagination
                                @current-change="handleCurrentChange"
                                :current-page="filter.page"
                                :page-size="filter.page_size"
                                layout="total, prev, pager, next, jumper"
                                :total="totalPageSize">
                        </el-pagination>
                    </div>


                </article>
            </div>
        </div>
    </div>
</template>

<style scrop lang="scss" type="text/scss">
    @import '~scss_vars';
    .network-article{
        .box{
            padding: 20px;
            background-color: #fff;
            border-radius: 5px;
            box-shadow: 0 2px 3px hsla(0,0%,4%,.1), 0 0 0 1px hsla(0,0%,4%,.1);
            display: block;
            padding: 1.25rem;
            margin-top:1.5rem;
            h4.title{
                font-weight:normal;
                margin:0;
                font-size:1rem;
                color: $font-color;
            }
            .date-range,
            .table-content
            {
                margin:1.25rem 0;
            }
        }
    }
    .filter-item {
        display: inline-block;
        margin-right: 20px;
    }
    .number {
        border-radius: 0;
    }
    .view-rest {
        margin-bottom: 10px;
    }
</style>

<script>
    import dateFormat from '../../module/DateFormat';
    import QuRequest from '../../core/QuRequest'

    const quRequest = new QuRequest();

    export default {
        data(){
            return {
                stat: {
                    today_audit: 0,
                    today_num: 0,
                    total_num: 0
                },
                list: [],
                classifyList: {},
                classifySelect: '',
                sourceType: {},
                sourceList: [],
                sourceSelect: '',
                dateRange: '',
                totalPageSize: 1,

                filter: {
                    page: 1,
                    page_size: 30,
                    start_date: dateFormat(-7),
                    end_date: dateFormat(),
                    title: ''
                },
                statistic: {},
                detailPathName: 'NetworkArticleAuditDetail',

                total_page: 1,
                disabled: true,
                loading: false
            }
        },
        methods: {
            entryAudit(){
                this.$router.push({
                    name: this.detailPathName,
                    query: {
                        channel: this.classifySelect,
                        source_type: this.sourceSelect
                    }
                })
            },
            handleCurrentChange(page){
                this.filter.page = page;
                //console.log(page, this.filter.page);
                this.getList();
            },
            formatDate(dateStr){
                if(dateStr){
                    let date = new Date(dateStr);
                    let y = date.getFullYear();
                    let m = date.getMonth() + 1;
                    let d = date.getDate();
                    return [y, this.addZero(m), this.addZero(d)].join('/');
                } else {
                    return '';
                }

            },
            addZero: function(num) {
                return (num > 9 ? '' : '0') + num
            },
            search(){
                this.getList();
            },
            getList(){
                this.$updateRouter(this.filter);
                let param = Object.assign({}, this.filter);
                this.loading = true;
                quRequest.send(quRequest.Apis.qukan.commentAudit.list,param).then(res => {
                    this.list = res.data.data || res.data.videos || [];
                    this.total_page = res.data.total_page || 1;
                    if(res.data.page){
                        this.filter.page = res.data.page;
                    }
                }).catch( res => {
                    this.$message({
                        message: res.message,
                        type: 'error'
                    });
                }).fin(() => {
                    this.loading = false;
                })
            },
            toView(index){
                this.$router.push({ name: this.detailPathName, query: {id : index}})
            },
            getStatistics(){
                if(this.statistic){
                    this.disabled = true;
                    quRequest.send(quRequest.Apis.qukan.commentAudit.statistics).then(res => {
                        this.stat = res.data;
                        this.disabled = false;
                    }).catch( res => {
                        this.$message({
                            message: res.message,
                            type: 'error'
                        });
                    })
                }
            }

        },
        mounted() {
            //获取分类
            quRequest.send(quRequest.Apis.qukan.content.getDefaultChannelList
            ).then(res => {
                let cList = res.data || [];
                cList.unshift({
                    id: '',
                    name: '不选择'
                });

                this.classifyList = cList;
            })

            //获取来源
            quRequest.send(quRequest.Apis.qukan.collect.getSourceTypeList
            ).then(res => {
                let sList = [{
                    id: '',
                    name: '不选择'
                }];
                let source = {};
                for (let item in res.data) {
                    Object.assign(source, res.data[item]);
                }
                for (let item in source) {
                    sList.push({
                        id: item,
                        name: source[item]
                    });
                }
                this.sourceType = source;
                this.sourceList = sList;
            });

            this.getStatistics();

            if (this.filter.title) {
                this.getList();
            }
        }
    }


</script>



