import ApiInfo from './ApiInfo.js';

class QukanApiInfo extends ApiInfo {
    constructor({
        path,
        type = 'get',
        timeout = 5000,
        headers,
        project = 'perm', // || qukan,
        author = '未定义'
    }) {
        super({
            path,
            type,
            timeout,
            headers,
            project,
            author
        });
    }
}

export default QukanApiInfo;



